export default function DivisionPage({ params }: { params: { id: string } }) {
  const id = params.id;

  return (
    <div style={{ padding: 40 }}>
      <h1>Division {id}</h1>

      {Array.from({ length: 9 }, (_, i) => i + 1).map(week => (
        <div key={week}>
          <a href={`/division/${id}/week/${week}`}>Week {week}</a>
        </div>
      ))}
    </div>
  );
}